yB;IACzB,8BAA8B;IAC9B,6BAA6B;IAC7B,4BAA4B;IAC5B,wBAAwB;IACxB,iBAAiB;IACjB,8BAA8B;AAClC;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,YAAY;IACZ,mBAAmB;IACnB,UAAU;IACV,gBAAgB;IAChB,gBAAgB;IAChB,iBAAiB;AACrB;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,sBAAsB;IACtB,cAAc;IACd,UAAU;AACd;AAEA;IACI,aAAa;IACb,eAAe;IACf,iBAAiB;AACrB;AAEA;IACI,YAAY;AAChB;;AAEA,YAAY;AACZ;IACI,2BAA2B;IAC3B,6BAA6B;IAC7B,yBAAyB;IACzB,UAAU;IACV,mCAAmC;IACnC,kCAAkC;IAClC,oBAAoB;IACpB,WAAW;IACX,YAAY;IACZ,wDAA+C;IAC/C,2DAAkD;AACtD;AAEA;AACI;QACI,4BAA4B;AAChC;AAEA;QACI,8BAA8B;AAClC;AACJ;AAEA;AACI;QACI,+BAA+B;AACnC;AAEA;QACI,iCAAiC;AACrC;AACJ;AAEA;AACI;QACI,4BAA4B;AAChC;AAEA;QACI,+BAA+B;AACnC;AACJ;AAEA;AACI;QACI,+BAA+B;AACnC;AAEA;QACI,kCAAkC;AACtC;AACJ;AAEA;IACI,WAAW;IACX,YAAY;IACZ,yBAAyB;IACzB,qBAAqB;IACrB,uBAAuB;IACvB,mCAAmC;IACnC,kCAAkC;IAClC,iBAAiB;AACrB","sourcesContent":["<script setup lang=\"ts\">\nimport { computed } from \"@vue/reactivity\";\nimport { defineAsyncComponent, onBeforeMount, onMounted, watch } from 'vue';\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\nimport { INotification } from \"../../../../domain/presentation/INotification\";\nimport { IStudio } from \"../../../../domain/useCase/IStudio\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\nimport \"bootstrap/js/dist/dropdown\";\nimport { IApplication, IExportViewItem, IModel, IModule, IFolder } from \"../../../../domain/model/models\";\nimport Tabs from \"../../../vue3/components/ui/tabs.vue\";\nimport { ITab, ITabs } from \"../../../vue3/components/ui/ITabs\";\nimport { ref } from \"vue\";\nimport { IGetExportSelectionData } from \"../../../../domain/useCase/IGetExportSelectionData\";\nimport { IRouter } from \"../../../../domain/presentation/IRouter\";\nimport ExportApp from \"../../components/application/exportApp.vue\";\nimport LivePreview from \"../../components/application/livePreview.vue\";\nimport { ILocalization } from \"../../../../domain/infrastructure/ILocalization\";\nimport { ExportType, IExportJob } from \"@stechquick/algae/lib/qCloudTemp/exporter\";\nimport { ILogout } from \"../../../../domain/useCase/ILogout\";\nimport { TooltipImpl } from \"../../impls/tooltipImpl\";\nimport { ITimer } from \"../../../../domain/infrastructure/ITimer\";\nimport RadioButtonComp from \"../radioButtonComp.vue\";\nimport { IUpdateApplication } from '../../../../domain/useCase/IUpdateApplication';\nimport { ILivePreviewUseCase } from \"../../../../domain/useCase/ILivePreviewUseCase\";\nimport { ICustomLivePreview, IDefaultLivePreview } from '@stechquick/algae/lib/qCloudTemp/livePreview';\nimport { IApplicationSettings } from \"../../../../domain/useCase/IApplicationSettings\";\nimport { IPage } from \"../../../../domain/presentation/IPage\";\nimport CheckboxComp from \"../checkboxComp.vue\";\nimport { IQJsonAdditionals } from \"@stechquick/algae/lib/qCloudTemp/quickCloud\";\n// import { watch } from \"fs\";\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst studio = container.resolve<IStudio>(IStudio);\nconst notification = container.resolve<INotification>(INotification);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst page = container.resolve<IPage>(IPage);\nconst livePreviwUseCase = container.resolve<ILivePreviewUseCase>(ILivePreviewUseCase);\nconst appSettingsCase = container.resolve<IApplicationSettings>(IApplicationSettings);\nconst selectionData = ref([] as unknown as Array<IExportViewItem>);\nconst router = container.resolve<IRouter>(IRouter);\nconst localization = container.resolve<ILocalization>(ILocalization);\nconst timer = container.resolve<ITimer>(ITimer);\nconst routerParams = router.getRouteParams<{ appID: string }>();\nconst checkedOut = computed(() => viewModel.studio.currentItem?.checkedOut);\nconst props = defineProps<{ app: IApplication }>();\nconst deployTip = ref(\"\");\nconst deployTooltipVisbile = ref(false);\nconst deployContentOpacity = ref(1);\nconst hoverValue = ref(false);\nconst tooltip = container.resolve<TooltipImpl>(TooltipImpl);\nconst previewType = ref<string>(\"default\");\nconst showCustomPreview = ref(false);\nconst previewDropdownOpen = ref(false);\nconst showQRCode = ref(false);\nconst showLoading = ref(true);\nconst customPreviewLastUpdated = ref();\nconst publishToWeb = ref(false);\nconst publishToMobile = ref(false);\n\nconst models = ref<Array<IModel>>([]);\n\nwatch(viewModel.studio.items, (_new, _old) => {\n    models.value = [];\n    getModel(viewModel.studio.items);\n})\n\nfunction getModel(items: Array<IModel | IModule | IFolder>) {\n    items.forEach(item => item.objectType == \"model\" ? models.value.push(item) : getModel(item.children))\n};\n\nconst hasModel = computed(() => models.value.length != 0);\nconst hasQJson = computed(() => models.value.some(model => model.modelType == \"qjson\"));\nconst hasRootPage = computed(() => models.value.some(model => model.modelType == \"qjson\" && (model.additionals as IQJsonAdditionals).isRootPage))\n\nconst isDeployAllowed = viewModel.user?.selectedDomain?.cloud && Object.keys(viewModel.user?.selectedDomain?.cloud).length == 1;\n\n\nconst operationState = {\n    publishing: ref(false),\n    downloading: ref(false),\n}\n\nonMounted(() => {\n    document.getElementById(\"ulDropdown\")?.addEventListener(\"click\", (event: any) => {\n        if (event.target.id == \"custom-preview\") return;\n        event.stopPropagation();\n    })\n    document.addEventListener(\"click\", () => {\n        const ariaExpanded = document.getElementById(\"dropdownLivePreviewButton\")!.getAttribute(\"aria-expanded\");\n        previewDropdownOpen.value = ariaExpanded == \"true\";\n    })\n})\n\nconst tablist = computed(() => {\n    return (\n        viewModel.studio?.openItems?.map((item) => {\n            const retVal: ITab = {\n                name: item.name,\n                type: item.modelType,\n                id: item.ID,\n                selected: viewModel.studio.currentItem?.ID == item.ID,\n                modified: item.modified?.model,\n                synced: item.synced,\n                cb: {\n                    close: executor.wrap(async () => studio.closeItem(item), { loading: false }),\n                    select: executor.wrap(async () => studio.openItem(item), { loading: true }),\n                },\n            };\n            return ref(retVal).value;\n        }) || []\n    );\n});\n\nfunction onCheckout() {\n    const currentItem = viewModel.studio.currentItem;\n    if (!currentItem) {\n        return;\n    }\n\n    executor.execute(async () => studio.checkout(currentItem), { loading: true });\n}\nfunction onSave() {\n    const currentItem = viewModel.studio.currentItem;\n    if (!currentItem) {\n        return;\n    }\n\n    executor.execute(async () => studio.save(currentItem), { loading: true });\n}\n\nfunction onSavePlus() {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(\"../dialogs/studio/savePlus.vue\")),\n        { closable: true }\n    );\n}\n\nasync function release() {\n    executor.execute(async () => studio.release(viewModel.studio.currentItem!), { loading: false });\n}\n\nfunction listJobs(jobType: IExportJob[\"type\"]) {\n    const title = jobType == \"export\" ? \"Export Job List\" : \"Deploy Job List\";\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../export/exportJobList.vue`)),\n        { closable: true, title, width: \"1000px\", maxHeight: \"500px\", overflow: \"auto\" },\n        { jobType }\n    );\n}\n\nfunction listLivePreviews() {\n\n    return;\n}\n\nasync function openExportDialog(exportType: ExportType) {\n    executor.execute(\n        async () => {\n            const applicationID = routerParams.appID;\n            selectionData.value = await executor.executeUseCase<IGetExportSelectionData>(IGetExportSelectionData, { applicationID, exportType });\n            dialog.showDialog(\n                ExportApp,\n                {\n                    closable: true,\n                    title: \"Export: \" + viewModel.studio.appName,\n                    image: { src: props.app?.logo || \"../../../../images/is-bankasi-logo.jpg\", width: \"50px\", height: \"50px\" },\n                    width: \"1000px\",\n                    maxHeight: \"700px\",\n                    height: \"635px\",\n                    overflow: \"hidden\",\n                },\n                { selectionData: selectionData.value, exportType }\n            );\n        },\n        { loading: true }\n    );\n}\n\nasync function goMainPage() {\n    if (viewModel.studio.openItems && viewModel.studio.openItems.some(item => item.checkedOut && item.modified?.model)) {\n        const result = await notification.showConfirm({\n            type: \"warning\",\n            title: `You are leaving the page.`,\n            text: \"Changes you made may not be saved.\",\n            showCloseIcon: true,\n            approveText: \"Confirm\",\n            cancelText: \"Cancel\",\n        });\n        if (result.result == \"cancel\") {\n            return;\n        }\n    }\n    router.push(\"/main/applicationList\");\n}\n\nfunction checkDeployConditions() {\n    return !viewModel.apps.find((app) => app.ID === viewModel.studio.appId)?.deployParameters?.gitProductRepoId;\n}\n\nfunction logout() {\n    executor.execute(\n        async () => {\n            await executor.executeUseCase<ILogout>(ILogout);\n            router.push(\"/login\");\n        },\n        { loading: true }\n    );\n}\n\nfunction showDeployTip() {\n    if (checkDeployConditions()) {\n        deployTip.value = localization.get(\"deployTip\");\n        return deployTip.value;\n    } else {\n        deployTip.value = \"\";\n        return deployTip.value;\n    }\n}\n\nfunction openCustomPreviewSettingsScreen() {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../dialogs/preview/customPreviewSettings.vue`)),\n        { closable: true, title: \"Custom Preview Settings\", width: \"800px\", height: \"705px\" }\n    );\n    document.getElementById(\"dropdownLivePreviewButton\")!.setAttribute(\"aria-expanded\", \"false\")\n}\n\nasync function openLivePreviewDialogScreen(exportType: ExportType) {\n    console.log(\"LivePreviewDialog\");\n    executor.execute(\n        async () => {\n            const applicationID = routerParams.appID;\n            selectionData.value = await executor.executeUseCase<IGetExportSelectionData>(IGetExportSelectionData, { applicationID, exportType });\n            dialog.showDialog(\n                LivePreview,\n                {\n                    closable: true,\n                    title: \"Live Preview: \" + viewModel.studio.appName,\n                    image: { src: props.app?.logo || \"../../../../images/is-bankasi-logo.jpg\", width: \"50px\", height: \"50px\" },\n                    width: \"1000px\",\n                    maxHeight: \"700px\",\n                    height: \"635px\",\n                    overflow: \"hidden\",\n                },\n                { selectionData: selectionData.value, exportType }\n            );\n        },\n        { loading: true }\n    );\n}\n\nfunction hoverLogo() {\n    hoverValue.value = true;\n}\nfunction unHover() {\n    hoverValue.value = false;\n}\nfunction deployBtnTooltipOver() {\n    deployTooltipVisbile.value = true;\n    deployContentOpacity.value = 1;\n    timer.reset();\n}\nfunction deployTooltipMouseLeave() {\n    deployTooltipVisbile.value = false;\n}\nfunction deployBtnTooltipMouseLeave() {\n    if (timer.isTimerRunning) {\n        return;\n    }\n    timer.setUp({\n        time: 3000,\n        callback: () => {\n            deployTooltipVisbile.value = false;\n            deployContentOpacity.value = 1;\n            timer.reset();\n        },\n        interval: {\n            time: 1000,\n            callback: () => {\n                deployContentOpacity.value = deployContentOpacity.value - 0.3;\n            },\n        },\n    });\n}\nfunction deployContentpOver() {\n    deployContentOpacity.value = 1;\n    timer.reset();\n}\nfunction openSettings() {\n    deployTooltipVisbile.value = false;\n}\n\nasync function checkPreviews() {\n    showLoading.value = true;\n    const ariaExpanded = document.getElementById(\"dropdownLivePreviewButton\")!.getAttribute(\"aria-expanded\");\n    if (ariaExpanded == \"false\") return;\n    let livePreviewList = await livePreviwUseCase.listLivePreviewInfo(viewModel.studio.appId);\n    showLoading.value = false;\n\n    if (livePreviewList && livePreviewList.length > 0) {\n        const customPreview = livePreviewList.find(lp => lp.type == \"custom\") as ICustomLivePreview;\n        const selectedPreview = livePreviewList.find(lp => lp.selected);\n        previewType.value = selectedPreview!.type;\n        if (!showQRCode.value) {\n            showQRCode.value = true;\n            const canvas = document.getElementById(\"qrcode\");\n            await livePreviwUseCase.createLivePreviewQRCode(canvas!, selectedPreview!.appID);\n        }\n        if (customPreview) {\n            const date = new Date(customPreview.creationInfo.date)\n            customPreviewLastUpdated.value = date.getDate() + \".\" + (date.getMonth() + 1) + \".\" + date.getFullYear() + \" \" + (date.getHours() < 10 ? \"0\" : \"\") + date.getHours() + \":\" + (date.getMinutes() < 10 ? \"0\" : \"\") + date.getMinutes();\n            showCustomPreview.value = true;\n        }\n    } else {\n        showCustomPreview.value = false;\n        previewType.value = \"default\";\n    }\n}\n\nasync function openLivePreviewTab() {\n    const appID = viewModel.studio.appId;\n    const appName = viewModel.studio.appName;\n\n    await executor.execute(async () => {\n        let lpItem = await livePreviwUseCase.getLivePreviewInfo(appID, { selected: true });\n        if (!lpItem) {\n            await livePreviwUseCase.createLivePreview({ appID, type: \"default\", selected: true });\n        }\n        window.open(`../../livepreview/${appID}/${appName}`, \"_blank\")\n    }, { loading: true });\n}\n\nfunction handleStudioOrCustom(selectedType: string) {\n    if (showCustomPreview.value) {\n        executor.execute(\n            async () => {\n                await livePreviwUseCase.updateSelectedLivePreview(viewModel.studio.appId);\n                previewType.value = selectedType;\n            },\n            { loading: true }\n        )\n    }\n}\n\nfunction publishApplication() {\n    operationState.publishing.value = !operationState.publishing.value;\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../dialogs/publish/publishDialog.vue`)),\n        { closable: true, title: \"Choose Modules to Publish\", width: \"800px\", height: \"622px\" },\n        { screenType: \"Deploy\" }\n    );\n}\n\nfunction downloadApplication() {\n    operationState.downloading.value = !operationState.downloading.value;\n\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../dialogs/publish/publishDialog.vue`)),\n        { closable: true, title: \"Chose Modules to Download\", width: \"800px\", height: \"531px\" },\n        { screenType: \"Export\" }\n    );\n}\n\nfunction seeProgressDetails() {\n    page.openPage(\n        defineAsyncComponent(() => import('../dialogs/publish/publishHistory.vue')),\n        {name : \"Progress Details\"}\n    );\n    //listJobs('export');\n}\nfunction checkPublishDetails() {\n    let currentApp = viewModel.apps.find((app) => app.ID == viewModel.studio.appId);\n    viewModel.studio.dnsEndpoint = currentApp?.cloud?.azure?.dnsEndpoint;\n    viewModel.studio.deployDate = currentApp?.cloud?.azure?.deployDate?.toLocaleString();\n\n}\nfunction calculateDate(createDate: string) {\n    const date = new Date(createDate);\n    if(date) {\n        const minutes = parseInt(String(((new Date()).getTime() - date.getTime()) / (60 * 1000)), 10)\n        const hours = parseInt(String(minutes / 60), 10);\n        const days = parseInt(String(hours / 24), 10);\n        const weeks = parseInt(String(days / 7), 10);\n        const months = parseInt(String(weeks / 4), 10);\n        const years = parseInt(String(months / 12), 10);\n        if(years > 0) {\n            return years.toFixed() + ` year${years < 2 ? '' : 's'} ago`;\n        } else if(months > 0) {\n            return months.toFixed() + ` month${months < 2 ? '' : 's'} ago`;\n        } else if(weeks > 0) {\n            return weeks.toFixed() + ` week${weeks < 2 ? '' : 's'} ago`;\n        } else if(days > 0) {\n            return days.toFixed() + ` day${days < 2 ? '' : 's'} ago`;\n        } else if(hours > 0) {\n            return hours.toFixed() + ` hour${hours < 2 ? '' : 's'} ago`;\n        } else if(minutes > 0) {\n            return minutes.toFixed() + ` minute${minutes < 2 ? '' : 's'} ago`;\n        }\n    }\n    return \"few seconds ago\";\n}\n</script>\n<template>\n    <div class=\"top-menu\">\n        <div class=\"row top-container\">\n            <div class=\"col-lg-1 home-section\" style=\"display: flex\">\n                <div class=\"corner-container\">\n                    <div class=\"logo-container\" @click=\"goMainPage\" @mouseover=\"hoverLogo()\" @mouseleave=\"unHover()\"\n                        :style=\"{ 'background-color': hoverValue ? '#151233' : '', padding: '15px' }\">\n                        <img v-if=\"!hoverValue\" src=\"../../../../images/ide5-logo.svg\" alt=\"ide5-logo\" width=\"24\"\n                            height=\"24\" />\n                        <img v-if=\"hoverValue\" src=\"../../../../images/plateau_hover.svg\" alt=\"ide5-logo\" />\n                    </div>\n                </div>\n                <div class=\"vl\"></div>\n            </div>\n\n            <div class=\"col-lg-8 middle-resize\">\n                <div class=\"middle-container\">\n                    <div class=\"title-container\">\n                        <img src=\"../../../../images/is-bankasi-logo.jpg\" alt=\"is-bankasi-logo\" width=\"24\" height=\"24\"\n                            class=\"title-logo\" />\n                        <p class=\"title\">{{ viewModel.studio.appName }}</p>\n                    </div>\n                    <div class=\"tabs\">\n                        <Tabs :tabs=\"tablist\" height=\"30px\" ref=\"tabComp\"></Tabs>\n                    </div>\n                </div>\n            </div>\n            <div class=\"col-lg-3 last-resize\">\n                <div class=\"end-container position-absolute top-0 end-0\">\n                    <!-- <button type=\"button\" class=\"btn-sm end-icons export-button\" id=\"-\" @click=\"openLivePreviewDialogScreen('qui')\"> \n            <i class=\" export\"></i><span class=\"export-text\"></span>\n          </button> -->\n\n                    \n                    <div style=\"display: flex; flex-direction: column; justify-content: center;\" :class=\"{'button-disabled': !hasQJson || !hasRootPage}\">\n                        <div class=\"preview-button-group\">\n                            <span style=\"margin-top: 7px;\">\n                                <div  type=\"button\" @click=\"openLivePreviewTab()\" class=\"preview-button\">\n                                    <span class=\"preview-play-icon\"><i class=\"mdi mdi-play mdi-20px\"> </i></span>\n                                    <span class=\"preview-text\">Preview</span>\n                                </div>\n                            </span>\n                            <span style=\"margin-top: 7px;\">\n                                <div type=\"button\" @click=\"checkPreviews\" class=\"preview-dropdown-button\"\n                                    id=\"dropdownLivePreviewButton\" data-bs-toggle=\"dropdown\" aria-expanded=\"false\">\n                                     <!-- <span class=\"preview-dropdown-icon\" :style=\"previewDropdownOpen ? 'transform: rotate(180deg); margin-top: 10px;' : ''\"></span> -->\n                                    <i class=\"mdi mdi-dots-vertical\" style=\"font-size: 20px\"></i>\n                                </div>\n\n                                <ul class=\"dropdown-menu menu-preview\" id=\"ulDropdown\"\n                                    :style=\"'box-shadow: 0px 2px 4px #00000029; border: 1px solid #D5D5D5;' + (showLoading ? 'height: 178px;' : showCustomPreview ? 'height: 523px' : showQRCode ? 'height: 485px;' : '')\"\n                                    aria-labelledby=\"dropdownLivePreviewButton\">\n                                    <div v-show=\"!showLoading\">\n                                        <li>\n                                            <div :style=\"{ margin: '25px 0px 0px 101px', width: '72.44px', height: '57.96px' }\">\n                                                <img src=\"../../../../images/projector.svg\" />\n                                            </div>\n                                        </li>\n                                        <li>\n                                            <span class=\"preview-dropdown-text\">Choose how would you like to preview your\n                                                application</span>\n                                        </li>\n                                        <hr class=\"preview-first-line\" />\n                                        <li>\n                                            <RadioButtonComp class=\"radio-button\" style=\"left: 20px\"\n                                                :active=\"previewType == 'default'\" @click=\"handleStudioOrCustom('default')\" />\n                                            <span class=\"preview-menu-studio-text\">Studio Preview</span>\n                                            <br />\n                                            <span class=\"preview-menu-studio-description\">Qui Preview</span>\n                                        </li>\n                                        <hr class=\"preview-second-line\" />\n                                        <li v-if=\"showCustomPreview\">\n                                            <RadioButtonComp class=\"radio-button\" style=\"left: 20px\"\n                                                :active=\"previewType == 'custom'\" @click=\"handleStudioOrCustom('custom')\" />\n                                            <span class=\"preview-menu-custom-text\">Custom Preview</span>\n                                            <br />\n                                            <span class=\"preview-menu-custom-description\">Last update {{ customPreviewLastUpdated }}</span>\n                                            <span id=\"custom-preview\" class=\"edit-custom-preview\"\n                                                @click=\"openCustomPreviewSettingsScreen()\">\n                                                <i class=\"mdi mdi-pencil\" style=\"font-size: 16px;\" /> Edit Custom Preview\n                                            </span>\n                                        </li>\n                                        <li v-if=\"!showCustomPreview\">\n                                            <div class=\"preview-menu-items\" @click=\"openCustomPreviewSettingsScreen()\">\n                                                <span id=\"custom-preview\" class=\"add-custom-preview\">\n                                                    <i class=\"mdi mdi-plus\" /> Add Custom Preview</span>\n                                            </div>\n                                        </li>\n                                        <hr class=\"preview-third-line\" v-show=\"showQRCode\"\n                                            :style=\"showCustomPreview ? 'margin-top: 69px;' : ''\" />\n                                        <li v-show=\"showQRCode || showCustomPreview\">\n                                            <span class=\"qrcode-text\">\n                                                You can also view your application on mobile by scanning QR code.\n                                            </span>\n                                            <canvas id=\"qrcode\" class=\"qrcode\" width=\"104\" height=\"104\"/>\n                                        </li>\n                                    </div>\n                                    <div v-show=\"showLoading\">\n                                        <div class=\"loading\"></div>\n                                        <div class=\"loading-text\">Loading</div>\n                                    </div>\n                                </ul>\n                            </span>\n                        </div>\n                        <span class=\"button-tooltip\" style=\"left: -70px; width: 280px\">\n                            To preview your app, you need to set up a start screen created outside of a module <p style=\"margin-bottom: 0\">Click <i style=\"background: #50505A\" class=\"mdi mdi-dots-horizontal\"></i> on UI Screen item that created outside of a module and use \"Set as Start screen\" option. </p> \n                        </span>\n                    </div>\n                    <!-- Publish -->\n                    <span style=\"margin-top: 7px; margin-left: 12px;\">\n                        <div :class=\"{'button-disabled': !hasModel}\">\n                            <button type=\"button\"\n                                :class=\"['publish-button', operationState.publishing.value ? '' : '']\"\n                                @click=\"checkPublishDetails();\"\n                                style=\"border:none; border-radius: 4px;  display: flex;\" id=\"dropdownPublishMenuButton\"\n                                data-bs-toggle=\"dropdown\" aria-expanded=\"false\">\n                                <i class=\"mdi mdi-share-all\" style=\"margin-left: 12px; font-size: 20px;\"></i>\n                                <!-- <div v-else class=\"spinner publishbutonsize\"></div> -->\n                                <span class=\"export-text\">Publish</span>\n                                <i class=\"mdi mdi-chevron-down\"></i>\n                            </button>\n                            <ul :class=\"['dropdown-menu menu-publish', operationState.publishing.value ? '' : '']\"\n                                :style=\"'box-shadow: 0px 2px 4px #00000029; border: 1px solid #D5D5D5;'\"\n                                aria-labelledby=\"dropdownLivePreviewButton\">\n                                <li @click=\"$event.stopPropagation()\">\n                                    <div\n                                        style=\"margin-top: 24px; display: flex; justify-content: center;\">\n                                        <img src=\"../../../../images/rocket-launch.svg\" />\n                                    </div>\n                                    <!-- <div v-else class=\"spinner\"></div> -->\n                                </li>\n                                <li style=\"display: flex; justify-content: center;\">\n                                    <span class=\"publish-dropdown-text\"\n                                        style=\"margin-top: 20px;\">From here, you can publish your application or see progress details.</span>\n                                    <!-- <span v-else class=\"publish-estimated-time-text\">Estimated time 15 minutes...</span> -->\n                                </li>\n                                <li @click=\"$event.stopPropagation()\">\n                                    <div class=\"device-type\" style=\"border-bottom: 1px solid #EEEEEE; border-top: 1px solid #D5D5D5\">\n                                        <CheckboxComp class=\"module-checkbox\" :check=\"publishToWeb\" @click=\"publishToWeb = !publishToWeb\" />\n                                        <span @click=\"publishToWeb = !publishToWeb\" class=\"deviceTypeName\">Web</span>\n                                        <div class=\"publishDesc\" v-if=\"viewModel.studio.deployDate && viewModel.studio.dnsEndpoint\">\n                                            <div style=\"margin-bottom: 4px\">This application is published to web <b>{{ calculateDate(viewModel.studio.deployDate) }}</b> </div>\n                                            <a class=\"pageLink\" :href=\"viewModel.studio.dnsEndpoint\" target=\"_blank\">Go to your web app\n                                                <i class=\"mdi mdi-open-in-new\"></i>\n                                            </a>\n                                        </div>\n                                        <div v-else class=\"publishDesc\">Publish your web application to Studio Playground.</div>\n                                    </div>\n                                    <div class=\"device-type\">\n                                        <CheckboxComp class=\"module-checkbox\" :disabled=\"true\" />\n                                        <span style=\"color:#D5D5D5\" class=\"deviceTypeName\">Mobile</span>\n                                        <div class=\"publishDesc\">Publish your mobile application to the Studio Apps in store.</div>\n                                    </div>\n                                </li>\n                                <li style=\"border-bottom: 1px solid #EEEEEE; border-top: 1px solid #EEEEEE; padding: 24px\">\n                                    <div :class=\"{'button-disabled': !isDeployAllowed}\">\n                                        <button type=\"button\" :disabled=\"(!publishToWeb && !publishToMobile) || !isDeployAllowed\" \n                                            :class=\"['publish-application-button']\"\n                                            @click=\"publishApplication()\">\n                                            <span class=\"publish-application-button-text\">Publish Application</span>\n                                        </button>\n                                        <span class=\"button-tooltip\" style=\"top: 410px; left: -16px; z-index: 1; width:320px\">\n                                            Your application publish definition has not been configured. <p style=\"margin-bottom: 0\">Connect with the Plateau Studio Team for assistance.</p>\n                                        </span>\n                                    </div>\n                                </li>\n                                <!-- <li>\n                                    <span v-if=\"!operationState.publishing.value\" class=\"publish-dropdown-text\">or you\n                                        can</span>\n                                    <span v-else class=\"publish-dropdown-text\">you can still</span>\n                                </li>\n                                <li :style=\"{ 'margin-top': '40px !important' }\">\n                                    <button type=\"button\"\n                                        :class=\"['download-application-button', operationState.publishing.value ? 'publishing' : '']\"\n                                        @click=\"downloadApplication()\">\n                                        <span v-if=\"!operationState.downloading.value\"\n                                            :class=\"['download-application-button-text', operationState.publishing.value ? 'publishing' : '']\">Download\n                                            Application</span>\n                                        <span v-else\n                                            :class=\"['download-application-button-text', operationState.publishing.value ? 'publishing' : '']\">Downloading...</span>\n                                    </button>\n                                </li> -->\n                                <!-- <hr :style=\"{ 'margin-top': '16px', 'border': (operationState.publishing.value ? '1px solid #2F3160' : '1px solid #D5D5D5') }\" /> -->\n                                <li style=\"padding: 20px 0 7px; justify-content: center; display: flex; align-items: center;\">\n                                    <span\n                                        :class=\"['publish-progress-details-text', operationState.publishing.value ? '' : '']\"\n                                        @click=\"seeProgressDetails()\">See Progress Details</span>\n                                </li>\n                            </ul>\n                            <span class=\"button-tooltip\" style=\"left: 70px; width: 280px\">\n                                Your application is currently empty. Create a new UI page or module to Publish.\n                            </span>\n                        </div>\n                    </span>\n                    <span style=\"margin-top: 5px; margin-right: 28px\"></span>\n                    <div class=\"navbar-icons col-1\">\n                        <div class=\"col-1 p-0 avatar-icon\" style=\"display: flex\">\n                            <i class=\"mdi mdi-account-circle profile-icon\" data-bs-toggle=\"dropdown\"\n                                aria-expanded=\"false\" data-bs-target=\"#dropdown-menu\" id=\"dropdownMenuButton1\"></i>\n                            <i class=\"mdi mdi-chevron-down chevronIcon\" data-bs-toggle=\"dropdown\" aria-expanded=\"false\"\n                                data-bs-target=\"#dropdown-menu\" id=\"dropdownMenuButton1\" style=\"padding-top: 16px\"></i>\n                            <ul style=\"width: 246px\" class=\"dropdown-menu\" aria-labelledby=\"dropdownMenuButton1\">\n                                <li>\n                                    <div class=\"menutem\" style=\"display: flex; margin: 9px\">\n                                        <i class=\"mdi mdi-account-circle\" style=\"font-size: 25px\"></i>\n                                        <a style=\"margin: 7px\">{{ viewModel.user?.email }}</a>\n                                    </div>\n                                </li>\n                                <li>\n                                    <div class=\"menutem\" style=\"display: flex; margin: 9px\" @click=\"logout()\">\n                                        <i class=\"mdi mdi-logout\" style=\"font-size: 25px\"></i>\n                                        <a style=\"margin: 7px\">Logout</a>\n                                    </div>\n                                </li>\n                            </ul>\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n<style scoped>\n@media (min-width: 500px) {\n    .home-section {\n        flex: 0 0 auto;\n        width: unset;\n    }\n}\n\n@media (max-width: 1108px) {\n    .middle-resize {\n        flex: 0 0 auto;\n        width: 60%;\n    }\n}\n\n@media (max-width: 910px) {\n    .middle-resize {\n        flex: 0 0 auto;\n        width: 52%;\n    }\n}\n\n@media (max-width: 820px) {\n    .middle-resize {\n        flex: 0 0 auto;\n        width: 46%;\n    }\n}\n\n@media (max-width: 694px) {\n    .middle-resize {\n        flex: 0 0 auto;\n        width: 38%;\n    }\n}\n\n@media (max-width: 575px) {\n    .middle-resize {\n        flex: 0 0 auto;\n        width: 28%;\n    }\n}\n\n.loading {\n  display: inline-block;\n  width: 70px;\n  height: 70px;\n  margin-left: calc((100%/2) - 35px);\n  margin-top: 22px;\n}\n.loading:after {\n  content: \" \";\n  display: block;\n  width: 70px;\n  height: 70px;\n  margin: 8px;\n  border-radius: 50%;\n  border: 6px solid #14ADFC;\n  border-color: #14ADFC transparent #14ADFC transparent;\n  animation: loading 1.2s linear infinite;\n}\n@keyframes loading {\n  0% {\n    transform: rotate(0deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n.loading-text {\n    color: #14ADFC;\n    font-family: 'Roboto';\n    font-size: 16px;\n    font-weight: 500;\n    margin-left: calc((100%/2) - 26px);\n    margin-top: 18px;\n}\n\n.loading-text:after {\n  overflow: hidden;\n  display: inline-block;\n  vertical-align: bottom;\n  -webkit-animation: ellipsis steps(4,end) 900ms infinite;      \n  animation: ellipsis steps(4,end) 900ms infinite;\n  content: \"\\2026\"; /* ascii code for the ellipsis character */\n  width: 0px;\n}\n\n@keyframes ellipsis {\n  to {\n    width: 1.25em;    \n  }\n}\n\n@-webkit-keyframes ellipsis {\n  to {\n    width: 1.25em;    \n  }\n}\n\n.qrcode-text {\n    width: 186px;\n    height: 32px;\n    text-align: center;\n    font: normal normal normal 12px/16px Roboto;\n    color: #A1A1A1;\n    position: absolute;\n    margin: 16px 0px 0px 44px;\n    margin-left: calc((100%/2) - 93px);\n}\n\n.qrcode {\n    height: 104px !important;\n    width: 104px !important;\n    margin-top: 65px;\n    margin-left: calc((100%/2) - 52px);\n}\n\n.avatar-icon {\n    margin-top: -15px !important;\n    color: #909090;\n    text-align: end;\n}\n\nheader {\n    height: 20px;\n    background: #3c3c3c;\n    color: gray;\n    display: flex;\n}\n\nbody {\n    margin: 0;\n    padding: 0;\n    overflow: hidden;\n    background-color: #eceff1 !important;\n}\n\n.top-menu {\n    flex: 0 1 67px;\n    max-height: 67px;\n    width: 100%;\n    border-bottom: 1px solid #d5d5d5;\n    background-color: #fff;\n}\n\n.corner-container {\n    margin-top: 6px;\n    margin-left: 6px;\n}\n\n.logo-container {\n    display: flex;\n    cursor: pointer;\n    width: 54px;\n    height: 54px;\n    border-radius: 4px;\n    margin-left: 4px;\n}\n\n/* .logo-container:hover{\n   background-color: #151233;\n    border-radius: 5px;\n} */\n\n.vl {\n    border-left: 1px solid #d5d5d5;\n    height: 46px;\n    margin-top: 10px;\n    margin-left: 12px;\n}\n\n.menu {\n    background-color: #151233 !important;\n    width: 223px !important;\n}\n\n.menu-item {\n    color: #fff !important;\n    font-weight: 600 !important;\n    margin-bottom: 4px;\n    font-size: 12px;\n}\n\n.menu-item:hover {\n    background-color: #151233 !importan